 import StrictlyGoods from '@/components/common/StrictlyGoods.vue'
  import AdvertisingPosition from '@/components/common/AdvertisingPosition.vue'
export default {
	data() {
		return {
			orderSn:''
		};
	},
	components:{
		StrictlyGoods,
		AdvertisingPosition
	},
	onLoad(options) {
		this.orderSn = options.orderSn
	},
	onShow() {
		
		
	},
	methods: {
	      // 广告位url跳转
	      goPath(url) {
	        if (url) {
	          location.href = url
	        }
	      },
	      // 查看订单详情
	      checkOrderDetal() {
	        // 详情需要 orderId, shopId
			uni.navigateTo({
				url:'/pages/order/goodsDetail/goodsDetail?orderId='+this.orderSn
			})
	      },
	      // 返回首页
	      goHome() {
			  uni.switchTab({
			  	url:'/pages/main/main'
			  })
	      }
	    }
}
